.prototype{
    font-family: "Inconsolata-Medium", monospace;
    color: #fff;
    z-index:1;
    margin-bottom: 10vw;
}
.p-img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 10px;
    box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.75);
    transition: all 0.3s ease-in-out;
    cursor: pointer;
}
.p-img:hover{
    transform: scale(1.1);
}

